<template>
  <div>
    <el-card class="g_label">
      <el-row justify="space-between" type="flex" :gutter="20" class="oneRow">
        <el-col :span="24">
          <div class="div_inline">
            时间:
            <el-date-picker
              class="el-date-picker-suffix"
              v-model="seekTime"
              type="datetimerange"
              range-separator="至"
              start-placeholder="开始时间"
              end-placeholder="结束时间"
              value-format="yyyy-MM-dd HH:mm:ss"
              @change="date_picker_change"
            ></el-date-picker>
          </div>
          <div class="div_inline">
            用户名:
            <el-input
              clearable
              size="small"
              class="el-input_co"
              style="width:132px"
              placeholder="请输入用户名"
              v-model="seekName"
              @keyup.enter.native="getList"
              @clear="getList"
            />
          </div>
          <div class="div_inline">
            操作类型:
            <el-input
              size="small"
              clearable
              class="el-input_co"
              style="width:132px"
              placeholder="请输入操作类型"
              v-model="seekOperationType"
              @keyup.enter.native="getList"
              @clear="getList"
            />
          </div>
          <div class="div_inline">
            <!-- 筛选按钮 -->
            <el-button @click="getList" class="el-button_co" type="primary" icon="el-icon-search">搜索</el-button>
          </div>
          <div class="div_inline div_right">日志总数: {{pageTotal}}</div>
        </el-col>
      </el-row>
      <el-row>
        <!-- 表格 -->
        <el-table
          style="width: 100%"
          :header-cell-style="{background:'#eee',color:'#000'}"
          :data="listdata"
          stripe
        >
          <el-table-column prop="logId" label="序号" align="center" width="55"></el-table-column>
          <el-table-column prop="operationModel" label="操作模块" align="center"></el-table-column>
          <el-table-column prop="operationFunction" label="接口功能" align="center"></el-table-column>
          <el-table-column prop="operationExplain" label="参数说明" align="center"></el-table-column>
          <el-table-column prop="accountInfo" label="用户名" align="center"></el-table-column>
          <el-table-column prop="ip" label="登陆IP" align="center"></el-table-column>
          <el-table-column prop="operationTime" label="时间" align="center"></el-table-column>
        </el-table>
      </el-row>
      <!-- 分页 -->
      <el-row>
        <div class="page_div">
          <el-pagination
            background
            @size-change="handleSizeChange"
            @current-change="handleCurrentChange"
            :current-page.sync="currentPage"
            :page-size="pageSize"
            layout="total, prev, pager, next"
            :total="pageTotal"
          ></el-pagination>
        </div>
      </el-row>
    </el-card>
  </div>
</template>
<script>
export default {
  data() {
    return {
      // 按时间筛选数据
      seekTime: ['', ''],
      // 按用户名筛选数据
      seekName: '',
      // 按操作类型筛选数据
      seekOperationType: '',
      // 设备表单数据
      listdata: [],
      currentPage: 1,
      pageSize: 13,
      pageTotal: 0
    }
  },
  created() {
    // console.log(new Date().format("yyyy-MM-dd hh:mm:ss"))
    // console.log(this.seekTime)
    this.isToken = JSON.parse(sessionStorage.getItem('token'))
    const timeStart = new Date()
    timeStart.setTime(timeStart.getTime() - 3600 * 1000 * 24 * 30)
    const timeEnd = new Date()
    this.seekTime = [
      timeStart.format('yyyy-MM-dd hh:mm:ss'),
      timeEnd.format('yyyy-MM-dd hh:mm:ss')
    ]
    this.getList()
    // this.initWebSocket()
  },
  methods: {
    // 获取公司列表数据
    getList() {
      var apiUrl =
        '/api' +
        '/system/log' +
        '?page=' +
        this.currentPage +
        '&pageSize=' +
        this.pageSize +
        '&startTime=' +
        this.seekTime[0] +
        '&endTime=' +
        this.seekTime[1]
      if (this.seekOperationType) {
        apiUrl += '&operationType=' + this.seekOperationType
      }
      if (this.seekName) {
        apiUrl += '&username=' + this.seekName
      }
      this.$http
        .get(apiUrl)
        .then(res => {
          this.listdata = res.data.data.list
          this.pageTotal = res.data.data.total
          //console.log(this.listdata)
          // this.listdata[1].passMessage = '体温过低'
        })
        // eslint-disable-next-line handle-callback-err
        .catch(err => {
          console.log('获取识别记录失败')
        })
    },
    pictrue(scope) {
      console.log(scope.row)
    },
    handlePictureCardPreview(file) {
      this.dialogImageUrl = file.url
      this.dialogVisible = true
      console.log(file.url)
    },
    // 分页
    handleSizeChange(val) {
      console.log(`每页 ${val} 条`)
    },
    handleCurrentChange(val) {
      console.log(`当前页: ${val}`)
      this.currentPage = val
      this.getList(this.currentPage)
    },
    date_picker_change(val) {
      console.log(`时间: ${val}`)
      console.log(this.seekTime)
      this.getList(this.currentPage)
    },
    // 获取是否戴口罩：
    getmaskImg(_mounthocc) {
      if (_mounthocc === '戴口罩') {
        return require('../../assets/img/mask01.png')
      } else {
        return require('../../assets/img/mask02.png')
      }
    },
    // 获取体温样式：
    gettemperatureClass(_passMessage) {
      if (_passMessage === '体温正常') {
        return 'temperature temperatureA'
      } else if (_passMessage === '体温过高') {
        return 'temperature temperatureB'
      } else if (_passMessage === '体温过低') {
        return 'temperature temperatureC'
      }
    }
  }
}
</script>
<style lang="less" scoped>
.temperature {
  width: 80px;
  height: 30px;
  margin: 0 auto;
  border-radius: 15px;
  text-align: left;
  line-height: 0px;
  font-size: 14px;
  font-weight: 600;
  border-width: 2px;
  margin-top: 0px;
  .temperature_div {
    margin-top: -6px;
  }
  .temperature_txt {
    margin-left: -5px;
    margin-top: 0px;
  }
  .temperature_ico {
    margin-left: 30px;
    margin-top: -14px;
  }
}
.temperatureA {
  color: #008e2b;
  border-color: #8cd28f;
}
.temperatureB {
  color: #ffffff;
  border-color: #e60012;
  background-color: #e60012;
}
.temperatureC {
  color: #ffffff;
  border-color: #0080ff;
  background-color: #0080ff;
}

.titleMessage {
  color: black;
  padding-bottom: 0.2rem;
  font-weight: 700;
  text-align: left;
  span {
    font-weight: 500;
    font-size: 0.1rem;
  }
}
.basicMessage {
  color: #3c4e86;
  b {
    display: inline-block;
    width: 1rem;
    text-align: right;
    height: 0.5rem;
    font-size: 0.2rem;
    padding-right: 0.1rem;
  }
  // input {
  //   height: 0.3rem;
  //   width: 3.5rem;
  // }
  em {
    width: 4rem;
    display: inline-block;
  }
  // .el-input {
  //   display: inline;
  // }
}
// 上传照片
.upPicture {
  position: relative;
  .uploadPicture {
    position: absolute;
    left: 1.17rem;
    top: 0.21rem;
    button {
      position: relative;
      width: 1rem;
      height: 0.45rem;

      span {
        position: absolute;
        left: 0.16rem;
        top: 0.13rem;
        font-size: 0.16rem;
      }
    }
  }
}
.baseImage {
  img {
    height: 80px;
  }
}
