<template>
  <div>
    <el-card class="g_label">
      <el-row justify="space-between" type="flex" :gutter="20" class="oneRow">
        <el-col :span="24">
          <div class="div_inline">
            设备IP/域名:
            <el-input
              size="small"
              clearable
              class="el-input_co"
              style="width:160px"
              placeholder="请输入设备IP/域名"
              v-model="seekIP"
            />
          </div>
          <div class="div_inline">
            设备序列号:
            <el-input
              size="small"
              clearable
              class="el-input_co"
              style="width:155px"
              placeholder="请输入设备序列号"
              v-model="seekSerialNo"
            />
          </div>
          <div class="div_inline">
            <!-- 筛选按钮 -->
            <el-button
              @click="getList"
              class="el-button_co"
              type="primary"
              icon="el-icon-search"
            >搜索</el-button>
          </div>
          <div class="div_inline div_right">
            <div class="div_inline">在线设备: {{pageTotal}}</div>
            <div class="div_inline">设备总数: {{pageTotal}}</div>
          </div>
        </el-col>
      </el-row>
      <el-row justify="space-between" type="flex" :gutter="20" class="twoRow">
        <el-col :span="24">
          <div class="div_inline_button">
            <!-- 添加按钮 -->
            <el-button
              @click="isAddDevice"
              class="el-button_co"
              type="primary"
              icon="el-icon-circle-plus-outline"
            >添加</el-button>
            <el-dialog title="添加设备信息" :visible.sync="addDevice.dialogVisible" width="30%">
              <div class="basicMessage">
                <div>
                  <b>公司:</b>
                  <em>
                    <el-input placeholder="请输入选择所属的公司" v-model="addDevice.CompanyName"></el-input>
                  </em>
                </div>
                <div>
                  <b>设备类型:</b>
                  <em>
                    <el-select v-model="addDevice.DeviceType" slot="prepend" placeholder="请输入设备类型">
                      <el-option label="面板机" value="facePlate"></el-option>
                      <el-option label="摄像头" value="camera"></el-option>
                    </el-select>
                  </em>
                </div>
                <div>
                  <b>设备地址:</b>
                  <em>
                    <el-input placeholder="请输入设备地址" v-model="addDevice.DeviceAddress"></el-input>
                  </em>
                </div>
                <div>
                  <b>端口:</b>
                  <em>
                    <el-input placeholder="请输入端口" v-model="addDevice.DevicePort"></el-input>
                  </em>
                </div>
                <div>
                  <b>IP/域名:</b>
                  <em>
                    <el-input placeholder="请输入IP/域名" v-model="addDevice.DeviceIp"></el-input>
                  </em>
                </div>
                <div>
                  <b>序列号:</b>
                  <em>
                    <el-input placeholder="请输入序列号" v-model="addDevice.DeviceSeria"></el-input>
                  </em>
                </div>
                <div>
                  <b>设备名称:</b>
                  <em>
                    <el-input placeholder="请输入设备名称" v-model="addDevice.DeviceName"></el-input>
                  </em>
                </div>
                <div>
                  <b>密码:</b>
                  <em>
                    <el-input
                      placeholder="请输入密码"
                      v-model="addDevice.DevicePassword"
                      type="password"
                    ></el-input>
                  </em>
                </div>
                <div>
                  <b>协议类型:</b>
                  <em>
                    <el-select
                      v-model="addDevice.ProtocolType"
                      slot="prepend"
                      placeholder="请输入设备类型"
                    >
                      <el-option label="私有协议" value="private"></el-option>
                      <el-option label="rtsp协议" value="rtsp"></el-option>
                    </el-select>
                  </em>
                </div>
              </div>
              <span slot="footer" class="dialog-footer">
                <el-button @click="addDevice.dialogVisible = false">取 消</el-button>
                <el-button type="primary" @click="isconfirm">保存</el-button>
              </span>
            </el-dialog>
          </div>
          <div class="div_inline_button">
            <!-- 删除 -->
            <el-button
              @click="isDel"
              class="el-button_co"
              type="danger"
              icon="el-icon-remove-outline"
            >删除</el-button>
          </div>
            <div class="div_inline_button">
            <!-- 导入 -->
              <el-button
                class="el-button_co"
                type="primary"
                icon="el-icon-upload2"
              >导入</el-button>
            </div>
            <div class="div_inline_button">
              <!-- 导出 -->
              <el-button
                class="el-button_co"
                type="primary"
                icon="el-icon-download"
              >导出</el-button>
            </div>
        </el-col>
      </el-row>
      <el-row>
        <!-- 表格 -->
        <el-table
          style="width: 100%"
          :header-cell-style="{background:'#eee',color:'#000'}"
          :data="listdata"
          @selection-change="handleSelectionChange"
        >
          <el-table-column type="selection" width="55"></el-table-column>
          <el-table-column label="序号" align="center" type="index" width="55"></el-table-column>
          <el-table-column prop="device_ip" label="IP" align="center"></el-table-column>
          <el-table-column prop="device_name" label="设备名称" align="center"></el-table-column>
          <el-table-column prop="serial" label="序列号" align="center"></el-table-column>
          <el-table-column prop="device_enabled" label="在线状态" align="center">
            <template slot-scope="scope">
              <i v-if="scope.row.device_enabled==0">
                <img src="../../assets/img/on_off02.png" />
              </i>
              <i v-else-if="scope.row.device_enabled==1">
                <img src="../../assets/img/on_off01.png" />
              </i>
            </template>
          </el-table-column>
          <el-table-column label="操作" align="center">
            <template slot-scope="scope">
              <!-- 编辑公司 -->
              <i style="padding: 0 0.08rem;" @click="isEdit(scope)">
                <img src="../../assets/img/a_edit01.png" />
              </i>
              <el-dialog title="修改企业信息" :visible.sync="isEditdialogVisible" width="30%">
                <div class="basicMessage">
                  <div>
                    <b>设备ID:</b>
                    <em>
                      <el-input v-model="scope.row.device_id" disabled></el-input>
                    </em>
                  </div>
                  <div>
                    <b>设备地址:</b>
                    <em>
                      <el-input placeholder="请输入设备地址" v-model="addDevice.DeviceAddress"></el-input>
                    </em>
                  </div>
                  <div>
                    <b>设备IP:</b>
                    <em>
                      <el-input placeholder="请输入设备IP" v-model="addDevice.DeviceIp"></el-input>
                    </em>
                  </div>
                  <div>
                    <b>设备端口:</b>
                    <em>
                      <el-input placeholder="请输入设备端口" v-model="addDevice.DevicePort"></el-input>
                    </em>
                  </div>
                  <div>
                    <b>设备名称:</b>
                    <em>
                      <el-input placeholder="请输入设备名称" v-model="addDevice.DeviceName"></el-input>
                    </em>
                  </div>
                  <div>
                    <b>设备密码:</b>
                    <em>
                      <el-input placeholder="请输入设备密码" v-model="addDevice.DevicePassword"></el-input>
                    </em>
                  </div>
                  <div>
                    <b>序列号:</b>
                    <em>
                      <el-input placeholder="请输入设备序列号" v-model="addDevice.DeviceSeria"></el-input>
                    </em>
                  </div>
                  <div>
                    <b>公司名称:</b>
                    <em>
                      <el-input placeholder="请输入公司名称" v-model="addDevice.CompanyName"></el-input>
                    </em>
                  </div>
                </div>
                <span slot="footer" class="dialog-footer">
                  <el-button @click="isEditdialogVisible= false">取 消</el-button>
                  <el-button type="primary" @click="isEditconfirm">保存</el-button>
                </span>
              </el-dialog>
              <!-- 删除公司 -->
              <i style="padding: 0 0.08rem;" @click="isDel(scope)">
                <img src="../../assets/img/a_del201.png" />
              </i>
            </template>
          </el-table-column>
        </el-table>
      </el-row>
      <!-- 分页 -->
      <el-row>
        <div class="page_div">
          <el-pagination
            background
            @size-change="handleSizeChange"
            @current-change="handleCurrentChange"
            :current-page.sync="currentPage"
            :page-size="pageSize"
            layout="total, prev, pager, next"
            :total="pageTotal"
          ></el-pagination>
        </div>
      </el-row>
    </el-card>
  </div>
</template>
<script>
export default {
  data() {
    return {
      // 设备表单数据
      listdata: [],
      // 添加设备数据
      addDevice: {
        dialogVisible: false,
        CompanyName: '',
        DevicePort: '',
        DeviceName: '',
        DevicePassword: '',
        DeviceAddress: '',
        DeviceIp: '',
        DeviceSeria: '',
        DeviceID: '',
        DeviceType: '',
        ProtocolType: ''
      },
      // 筛选ip
      seekIP: '',
      seekSerialNo: '',
      isToken: '',
      isEditdialogVisible: false,
      delId: [],
      currentPage: 1,
      pageSize: 13,
      pageTotal: 0
    }
  },
  created() {
    this.isToken = JSON.parse(sessionStorage.getItem('token'))
    this.getList()
  },
  methods: {
    getList() {
      var apiUrl =
        '/api' +
        '/device/find/list' +
        '?page=' +
        this.currentPage +
        '&pageSize=' +
        this.pageSize
      if (this.seekIP) {
        apiUrl += '&deviceIp=' + this.seekIP
      }
      if (this.seekSerialNo) {
        apiUrl += '&serialNo=' + this.seekSerialNo
      }
      this.$http
        .get(apiUrl)
        .then(res => {
          this.listdata = res.data.data.list
          this.pageTotal = res.data.data.total
        })
        // eslint-disable-next-line handle-callback-err
        .catch(err => {
          console.log('获取人员信息失败')
        })
    },
    // 分页
    handleSizeChange(val) {
      console.log(`每页 ${val} 条`)
    },
    handleCurrentChange(val) {
      console.log(`当前页: ${val}`)
      this.currentPage = val
      this.getList(this.currentPage)
    },
    date_picker_change(val) {
      console.log(`时间: ${val}`)
      console.log(this.seekTime)
      this.getList(this.currentPage)
    },
    // 添加公司按钮
    isAddDevice() {
      this.addDevice.dialogVisible = true
    },
    // 保存按钮
    isconfirm() {
      this.$http
        .post(
          '/api' + '/device/add ',
          {
            device_ip: this.addDevice.DeviceIp,
            device_port: this.addDevice.DevicePort + 0,
            device_name: this.addDevice.DeviceName,
            device_password: this.addDevice.DevicePassword,
            device_address: this.addDevice.DeviceAddress,
            serial: this.addDevice.DeviceSeria,
            company_name: this.addDevice.CompanyName,
            protocolType: this.addDevice.ProtocolType,
            deviceType: this.addDevice.DeviceType
          }
        )
        .then(res => {
          this.getList()
          // 添加完之后表单置空
          this.addDevice.dialogVisible = false
          this.addDevice.DeviceIp = null
          this.addDevice.DevicePort = null
          this.addDevice.DeviceName = null
          this.addDevice.DevicePassword = null
          this.addDevice.DeviceAddress = null
          this.addDevice.DeviceSeria = null
          this.addDevice.CompanyName = null
        })
        // eslint-disable-next-line handle-callback-err
        .catch(err => {
          console.log('添加公司失败')
        })
    },
    // 删除设备按钮
    isDelCompany() {},
    // 复选框选择的时候
    handleSelectionChange(val) {
      val.forEach((ele, indx) => {
        this.delId.push(ele.device_id)
      })
    },
    // 编辑设备按钮
    isEdit(scope) {
      this.isEditdialogVisible = true
      console.log(scope.row)
      this.addDevice.DeviceIp = scope.row.device_ip
      this.addDevice.DevicePort = scope.row.device_port
      this.addDevice.DeviceName = scope.row.device_name
      this.addDevice.DevicePassword = scope.row.device_password
      this.addDevice.DeviceSeria = scope.row.serial
      this.addDevice.CompanyName = scope.row.company_name
      this.addDevice.DeviceID = scope.row.device_id
      this.addDevice.DeviceAddress = scope.row.device_address
    },
    // 保存修改按钮
    isEditconfirm() {
      this.$http
        .post(
          '/api' + '/device/update',
          {
            device_id: this.addDevice.DeviceID,
            device_ip: this.addDevice.DeviceIp,
            device_port: this.addDevice.DevicePort + 0,
            device_name: this.addDevice.DeviceName,
            device_password: this.addDevice.DevicePassword,
            device_address: this.addDevice.DeviceAddress,
            serial: this.addDevice.DeviceSeria,
            company_name: this.addDevice.CompanyName,
            protocolType: this.addDevice.ProtocolType,
            deviceType: this.addDevice.DeviceType
          }
        )
        .then(res => {
          this.isEditdialogVisible = false
        })
        // eslint-disable-next-line handle-callback-err
        .catch(err => {
          console.log('保存修改按钮失败！')
        })
      this.getList()
    },
    // 删除设备按钮
    async isDel(scope) {
      if (scope.row) {
        this.delId.push(scope.row.device_id)
        console.log(this.delId)
      } else if (!this.delId.length) {
        this.$message({ message: '请您选择您需要删除的人员!', type: 'warning' })
        return false
      }
      const confirmResult = await this.$confirm(
        '您确定要删除吗?',
        '删除人员信息',
        {
          confirmButtonText: '确定',
          cancelButtonText: '取消',
          type: 'warning'
        }
      ).catch(err => err)
      if (confirmResult !== 'confirm') {
        return this.$message.info('已取消删除')
      }
      this.$http
        .post('/api' + '/device/del', this.delId)
        .then(res => {
          this.getList()
          console.log(res)
        })
        // eslint-disable-next-line handle-callback-err
        .catch(err => {
          console.log('添加公司失败')
        })
    }
  }
}
</script>
<style lang="less" scoped>
.titleMessage {
  color: black;
  padding-bottom: 0.2rem;
  font-weight: 700;
  text-align: left;
  span {
    font-weight: 500;
    font-size: 0.1rem;
  }
}
.basicMessage {
  color: #3c4e86;
  b {
    display: inline-block;
    width: 1rem;
    text-align: right;
    height: 0.5rem;
    font-size: 0.2rem;
    padding-right: 0.1rem;
  }
  // input {
  //   height: 0.3rem;
  //   width: 3.5rem;
  // }
  em {
    width: 4rem;
    display: inline-block;
  }
  // .el-input {
  //   display: inline;
  // }
}
// 上传照片
.upPicture {
  position: relative;
  .uploadPicture {
    position: absolute;
    left: 1.17rem;
    top: 0.21rem;
    button {
      position: relative;
      width: 1rem;
      height: 0.45rem;

      span {
        position: absolute;
        left: 0.16rem;
        top: 0.13rem;
        font-size: 0.16rem;
      }
    }
  }
}
</style>
