<template>
  <div>
    <el-card class="g_label">
      <el-row justify="space-between" type="flex" :gutter="20" class="oneRow">
        <el-col :span="24">
          <div class="div_inline">
            时间:
            <el-date-picker
              class="el-date-picker-suffix"
              v-model="seekTime"
              type="datetimerange"
              range-separator="至"
              start-placeholder="开始时间"
              end-placeholder="结束时间"
              value-format="yyyy-MM-dd HH:mm:ss"
              @change="date_picker_change"
            ></el-date-picker>
          </div>
          <div class="div_inline">
            <el-checkbox v-model="unusual" label="异常筛选" @change="date_picker_change" size="small" border></el-checkbox>
            <!-- <el-checkbox-button :label="unusual" :key="unusual">异常筛选</el-checkbox-button> -->
          </div>
          <div class="div_inline">
            用户名:
            <el-input
              clearable
              size="small"
              class="el-input_co"
              style="width:132px"
              placeholder="请输入用户名"
              v-model="seekName"
              @keyup.enter.native="getList"
              @clear="getList"
            />
          </div>
          <div class="div_inline">
            设备:
            <el-input
              size="small"
              clearable
              class="el-input_co"
              style="width:132px"
              placeholder="请输入序列号"
              v-model="seekSerialNo"
              @keyup.enter.native="getList"
              @clear="getList"
            />
          </div>
          <div class="div_inline_button">
            <!-- 筛选按钮 -->
            <el-button @click="getList" class="el-button_co" type="primary" icon="el-icon-search">搜索</el-button>
          </div>
          <div class="div_inline_button">
            <!-- 刷新按钮 -->
            <el-button @click="getList(true)" class="el-button_co" type="success" icon="el-icon-refresh">刷新</el-button>
          </div>
          <div class="div_inline div_right">记录总数: {{pageTotal}}</div>
          <!-- <span> 设备：
            <el-select v-model="devicename"
                       slot="prepend"
                       placeholder="请选择公司">
              <el-option v-for="item in devicename"
                         :key="item"
                         :label="item"
                         :value="item">
              </el-option>
            </el-select>
          </span>-->
        </el-col>
      </el-row>
      <el-row>
        <!-- 表格 -->
        <el-table
          style="width: 100%"
          :header-cell-style="{background:'#eee',color:'#000'}"
          :data="listdata"
          stripe
        >
          <el-table-column prop="info_id" label="序号" align="center" width="55"></el-table-column>
          <el-table-column prop="base64_picture" label="帧图" align="center">
            <template slot-scope="scope">
              <div class="baseImage">
                <img :src="getimgurl(scope.row.base64_picture)" />
              </div>
            </template>
          </el-table-column>
          <el-table-column prop="temperature" label="体温" align="center">
            <template slot-scope="scope">
              <el-card shadow="never" :class="gettemperatureClass(scope.row.passMessage)">
                <div class="temperature_div">
                  <span class="temperature_txt">{{scope.row.temperature}}</span>
                  <img class="temperature_ico" :src="getmaskImg(scope.row.mounthocc)" />
                </div>
              </el-card>
            </template>
          </el-table-column>
          <el-table-column prop="name" label="用户名" align="center"></el-table-column>
          <el-table-column prop="passResult" label="是否通行" align="center"></el-table-column>
          <el-table-column prop="serialNo" label="设备序列号" align="center"></el-table-column>
          <el-table-column prop="create_time" label="识别时间" align="center"></el-table-column>
        </el-table>
      </el-row>
      <!-- 分页 -->
      <el-row>
        <div class="page_div">
          <el-pagination
            background
            @size-change="handleSizeChange"
            @current-change="handleCurrentChange"
            :current-page.sync="currentPage"
            :page-size="pageSize"
            layout="total, prev, pager, next"
            :total="pageTotal"
          ></el-pagination>
        </div>
      </el-row>
    </el-card>
  </div>
</template>
<script>
export default {
  data() {
    return {
      // 按时间筛选数据
      seekTime: ['', ''],
      // 异常筛选：
      unusual: false,
      // 按用户名筛选数据
      seekName: '',
      // 按设备序列号筛选数据
      seekSerialNo: '',
      // 设备表单数据
      listdata: [],
      // 选择设备
      devicename: [],
      currentPage: 1,
      pageSize: 6,
      pageTotal: 0
    }
  },
  created() {
    // console.log(new Date().format("yyyy-MM-dd hh:mm:ss"))
    // console.log(this.seekTime)
    var timeStart = new Date()
    timeStart.setTime(timeStart.getTime() - 3600 * 1000 * 24 * 30)
    var timeEnd = new Date()
    this.seekTime = [
      timeStart.format('yyyy-MM-dd hh:mm:ss'),
      timeEnd.format('yyyy-MM-dd hh:mm:ss')
    ]
    this.getList()
  },
  methods: {
    getList(_res) {
      if (_res) {
        var timeStart = new Date()
        timeStart.setTime(timeStart.getTime() - 3600 * 1000 * 24 * 30)
        var timeEnd = new Date()
        this.seekTime = [
          timeStart.format('yyyy-MM-dd hh:mm:ss'),
          timeEnd.format('yyyy-MM-dd hh:mm:ss')
        ]
      }
      var apiUrl =
        '/api' +
        '/device/find/info' +
        '?page=' +
        this.currentPage +
        '&pageSize=' +
        this.pageSize +
        '&startTime=' +
        this.seekTime[0] +
        '&endTime=' +
        this.seekTime[1] +
        '&unusual=' +
        this.unusual
      if (this.seekSerialNo) {
        apiUrl += '&serialNo=' + this.seekSerialNo
      }
      if (this.seekName) {
        apiUrl += '&username=' + this.seekName
      }
      this.$http
        .get(apiUrl)
        .then(res => {
          this.listdata = res.data.data.list
          this.pageTotal = res.data.data.total
          // this.listdata[1].passMessage = '体温过低'
        })
        // eslint-disable-next-line handle-callback-err
        .catch(err => {
          console.log('获取识别记录失败')
        })
    },
    // 查询设备按钮http://localhost:8085/device/find/info?startTime=2020-03-04 18:10:21&endTime=2020-03-04 18:15:23&username=黄新乐
    pictrue(scope) {
      console.log(scope.row)
    },
    handlePictureCardPreview(file) {
      this.dialogImageUrl = file.url
      this.dialogVisible = true
      console.log(file.url)
    },
    // 分页
    handleSizeChange(val) {
      console.log(`每页 ${val} 条`)
    },
    handleCurrentChange(val) {
      console.log(`当前页: ${val}`)
      this.currentPage = val
      this.getList(this.currentPage)
    },
    date_picker_change(val) {
      console.log(`时间: ${val}`)
      console.log(this.seekTime)
      this.getList(this.currentPage)
    },
    // 获取是否戴口罩：
    getmaskImg(_mounthocc) {
      if (_mounthocc === '戴口罩') {
        return require('../../assets/img/mask01.png')
      } else {
        return require('../../assets/img/mask02.png')
      }
    },
    // 获取体温样式：
    gettemperatureClass(_passMessage) {
      if (_passMessage === '体温正常') {
        return 'temperature temperatureA'
      } else if (_passMessage === '体温过高') {
        return 'temperature temperatureB'
      } else if (_passMessage === '体温过低') {
        return 'temperature temperatureC'
      }
    },
    // 获取照片地址:
    getimgurl(_url) {
      if (_url.length === 0 || _url.substr(0, 4) === 'http') {
        return _url
      } else {
        return 'data:image/jpg;base64,' + _url
      }
    }
  }
}
</script>
<style lang="less" scoped>
.temperature {
  width: 80px;
  height: 30px;
  margin: 0 auto;
  border-radius: 15px;
  text-align: left;
  line-height: 0px;
  font-size: 14px;
  font-weight: 600;
  border-width: 2px;
  margin-top: 0px;
  .temperature_div {
    margin-top: -6px;
  }
  .temperature_txt {
    margin-left: -5px;
    margin-top: 0px;
  }
  .temperature_ico {
    margin-left: 30px;
    margin-top: -14px;
  }
}
.temperatureA {
  color: #008e2b;
  border-color: #8cd28f;
}
.temperatureB {
  color: #ffffff;
  border-color: #e60012;
  background-color: #e60012;
}
.temperatureC {
  color: #ffffff;
  border-color: #0080ff;
  background-color: #0080ff;
}

.titleMessage {
  color: black;
  padding-bottom: 0.2rem;
  font-weight: 700;
  text-align: left;
  span {
    font-weight: 500;
    font-size: 0.1rem;
  }
}
.basicMessage {
  color: #3c4e86;
  b {
    display: inline-block;
    width: 1rem;
    text-align: right;
    height: 0.5rem;
    font-size: 0.2rem;
    padding-right: 0.1rem;
  }
  // input {
  //   height: 0.3rem;
  //   width: 3.5rem;
  // }
  em {
    width: 4rem;
    display: inline-block;
  }
  // .el-input {
  //   display: inline;
  // }
}
// 上传照片
.upPicture {
  position: relative;
  .uploadPicture {
    position: absolute;
    left: 1.17rem;
    top: 0.21rem;
    button {
      position: relative;
      width: 1rem;
      height: 0.45rem;

      span {
        position: absolute;
        left: 0.16rem;
        top: 0.13rem;
        font-size: 0.16rem;
      }
    }
  }
}
.baseImage {
  img {
    height: 80px;
  }
}
