<template>
  <div>
    <el-card class="g_label">
      <div slot="header" class="card-title clearfix">
        <span>体温报警阈值配置</span>
      </div>
      <el-row justify="space-between" type="flex" :gutter="20" class="oneRow">
        <el-col :span="24">
          <div class="div_content div_top">
            <span class="span_left">最低温:</span>
            <el-input-number
              size="small"
              v-model="lt"
              :precision="1"
              :step="0.1"
              :max="36"
              :min="30"
            ></el-input-number>
          </div>
          <div class="div_content div_button">
            <span class="span_left">最高温:</span>
            <el-input-number
              size="small"
              v-model="gt"
              :precision="1"
              :step="0.1"
              :max="40"
              :min="36"
            ></el-input-number>
          </div>
        </el-col>
      </el-row>
      <el-row justify="space-between" type="flex" :gutter="20" class="twoRow">
        <el-col :span="24">
          <el-divider></el-divider>
          <div class="div_content div-bottom-button div_right">
            <!-- 保存按钮 -->
            <el-button
              @click="setLimit"
              class="el-button_co"
              type="primary"
              :icon="saveing ? 'el-icon-loading' : 'el-icon-circle-check'"
            >保存</el-button>
          </div>
        </el-col>
      </el-row>
    </el-card>
  </div>
</template>
<script>
export default {
  data() {
    return {
      gt: 0, // 最高温(double)
      lt: 0, // 最低温(double)
      saveing: false // 是否正在保存
    }
  },
  created() {
    // console.log(new Date().format("yyyy-MM-dd hh:mm:ss"))
    // console.log(this.seekTime)
    this.isToken = JSON.parse(sessionStorage.getItem('token'))
    this.getLimit()
  },
  methods: {
    getLimit() {
      this.$http
        .get('/api' + '/device/find/limit')
        .then(res => {
          console.log('res.data.data:', res.data.data)
          if (res.data && res.data.code === 0 && res.data.data) {
            console.log('res.data.data.lt', res.data.data.lt)
            console.log('res.data.data.gt:', res.data.data.gt)
            this.configGd.limit = [res.data.data.lt, res.data.data.gt]
            this.lt = res.data.data.lt
            this.gt = res.data.data.gt
          }
          console.log('this.configGd.limit:', this.configGd.limit)
        })
        .catch(err => {
          this.$message.error({
            message: '查询体温报警阀值失败'
          })
        })
    },
    setLimit() {
      this.saveing = true
      this.$http
        .get(
          '/api' + '/device/set/limit' + '?lt=' + this.lt + '&gt=' + this.gt
        )
        .then(res => {
          this.saveing = false
          console.log('res.data:', res.data)
          if (res.data && res.data.code === 0) {
            this.$message({
              message: '设置体温报警阀值成功',
              type: 'success'
            })
          }
        })
        .catch(err => {
          this.saveing = false
          this.$message.error({
            message: '设置体温报警阀值失败！'
          })
        })
    }
  }
}
</script>
<style lang="less" scoped>
</style>
